import { Fragment } from "react";
import { Helmet } from "react-helmet";
import { makeStyles } from "@material-ui/core";
import { Button } from "@mui/material";
import { Theme } from "@mui/system";
import { Link, useHistory } from "react-router-dom";

const useStyles = makeStyles((theme: Theme) => ({
  hero: {
    alignItems: "center",
    display: "flex",
    flexDirection: "column",
    height: "100vh",
    margin: "0 auto",
    maxWidth: "90%",
    paddingTop: "55px",
    rowGap: "33px",
    width: "100%",

    "& .not-found": {
      height: "auto",
      maxWidth: "100%",
      marginBottom: "16px",
    },

    "& .heading": {
      color: "#000000",
      fontSize: "24px",
      fontWeight: 500,
      lineHeight: "28px",
      textAlign: "center",
    },

    "& .actions": {
      alignItems: "center",
      display: "flex",
      justifyContent: "center",
      columnGap: "20px",

      "& .MuiButton-root": {
        height: "48px",
        width: "105px",
        justifyContent: "center",
      },
    },
  },

  [theme.breakpoints.up("md")]: {
    hero: {
      maxWidth: "100%",
      paddingTop: "66px",
    },
  },
}));

const UnAuthorized = () => {
  const classes = useStyles();
  const history = useHistory();

  return (
    <Fragment>
      <Helmet>
        <title>403 UnAuthorized</title>
      </Helmet>
      <section className={classes.hero}>
        <img
          src={"/images/404.svg"}
          alt="404"
          className="not-found"
          loading="lazy"
          height={447}
          width={677}
        />
        <h1 className="heading">
          {"Oops! You are Unauthorized to access that page."}
        </h1>
        <div className="actions">
          <Button variant="outlined" color="primary" component={Link} to="/">
            {"Home"}
          </Button>
          <Button
            variant="contained"
            disabled={history.action === "POP"}
            onClick={() => {
              if (history.action !== "POP") {
                history.goBack();
              } else {
                history.replace("/engage/chat");
              }
            }}
          >
            {"Go Back"}
          </Button>
        </div>
      </section>
    </Fragment>
  );
};

export default UnAuthorized;
